$prefix: '.rise-blocks';

.wp-block-rise-blocks-button {
    min-width: 150px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    border: 1px solid transparent;
    
    &#{$prefix}-icon-left {
        i {
            order: 1;
            margin-right: 10px;
        }

        span {
            order: 2;
        }
    }

    &#{$prefix}-icon-right {
        i {
            order: 2;
            margin-left: 10px;
        }

        span {
            order: 1;
        }
    }
}